import React, { Component } from 'react';
import { connect } from "react-redux";

class SaladBoxeDressing extends Component {

    constructor(props) {
        super(props);
        this.state = {
            select: 'Select Dressing...'
        }

        this.handleSelectDressing = this.handleSelectDressing.bind(this);
    }

    handleSelectDressing(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;

        this.props.dispatch({
            type: 'SaladBox_Dressing',
            name: this.props.name,
            dressing: value
        });

        this.setState({
            selectReset: 'Select Dressing...'
        })


    }

    handleRemoveDressing(event) {
        if (event.target.dataset.dressing !== undefined){
            this.props.dispatch({
                type: 'SaladBox_Dressing_Remove',
                name: this.props.name,
                dressing: event.target.dataset.dressing
            });
        }
    }

    render() {
        return (
            <div className={'wrap-select-extra' + (this.props.globalState.missingDressing[this.props.name] > 0 ? ' primary' : (this.props.globalState.missingDressing[this.props.name] < 0 ? ' warning' : ''))}>

                {
                    this.props.globalState.missingDressing[this.props.name] < 0 ?
                    <p><b>You need to remove dressing</b></p> :
                    null
                }

                {
                    this.props.globalState.missingDressing[this.props.name] > 0 ?
                    <div className="form-group row select-dressing">
                        <label className="col-sm-12 col-form-label" htmlFor={'select_' + this.props.name + '_dressing'}><b>Select {this.props.globalState.missingDressing[this.props.name]} Dressing</b></label>
                        <div className="col-sm-12">
                            <select
                                value={this.state.select}
                                className="form-control"
                                id={'select_' + this.props.name + '_dressing'}
                                onChange={this.handleSelectDressing}
                            >
                                <option>Select Dressing...</option>
                                <option>Ranch</option>
                                <option>Italian</option>
                                <option>Caesar</option>
                            </select>
                        </div>
                    </div>
                    :
                    null
                }

                {
                    this.props.globalState[this.props.name].dressing.Ranch !== 0 ?
                    <div className="badge badge-secondary saladbox-badge">

                        <div className="badge-name">
                            Ranch
                            <div className="badge badge-light">{this.props.globalState[this.props.name].dressing.Ranch}</div>
                        </div>

                        <button type="button" className="close" data-dressing="Ranch" onClick={this.handleRemoveDressing.bind(this)}>
                            <span aria-hidden="true" data-dressing="Ranch">x</span>
                        </button>

                    </div>
                    :
                    null
                }

                {
                    this.props.globalState[this.props.name].dressing.Italian !== 0 ?
                    <div className="badge badge-secondary saladbox-badge">

                        <div className="badge-name">
                            Italian
                            <div className="badge badge-light">{this.props.globalState[this.props.name].dressing.Italian}</div>
                        </div>

                        <button type="button" className="close" data-dressing="Italian" onClick={this.handleRemoveDressing.bind(this)}>
                            <span aria-hidden="true" data-dressing="Italian">x</span>
                        </button>

                    </div>
                    :
                    null
                }

                {
                    this.props.globalState[this.props.name].dressing.Caesar !== 0 ?
                    <div className="badge badge-secondary saladbox-badge">

                        <div className="badge-name">
                            Caesar
                            <div className="badge badge-light">{this.props.globalState[this.props.name].dressing.Caesar}</div>
                        </div>

                        <button type="button" className="close" data-dressing="Caesar" onClick={this.handleRemoveDressing.bind(this)}>
                            <span aria-hidden="true" data-dressing="Caesar">x</span>
                        </button>

                    </div>
                    :
                    null
                }

            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    globalState: state
});

export default connect(mapStateToProps)(SaladBoxeDressing);


