import React, { Component } from 'react';
import { connect } from "react-redux";

import BreakfastCard from './summaryCards/breakfastCard';
import BeverageCard from './summaryCards/beverageCard';
import DeliTrayCard from './summaryCards/deliTrayCard';
import SaladSidesCard from './summaryCards/saladSidesCard';
import SaladBoxCard from './summaryCards/saladBoxCard';
import LunchBoxCard from './summaryCards/lunchBoxCard';
import DessertCard from './summaryCards/dessertCard';
import SubSandwichCard from './summaryCards/subSandwichCard';
import ExtraChips from './summaryCards/extraChips';
import ExtraBeverages from './summaryCards/extraBeverages.js';

const roundTo = require('roundto');

class Summary extends Component {

    constructor(props) {
        super(props);

        this.state = {
            tax: roundTo(this.props.state.orderTotal * 0.061, 2)
        }

        this.props.dispatch({
            type: 'navIndex',
            payload: 8
        })

    }

    handleTextarea = event => {
        this.props.dispatch({
            type: 'addtionalNote',
            payload: event.target.value
        })
    }

    handleSubmit = () => {
        // console.log('state = ', this.props.state);
        this.props.history.push('/Confirmation');
    }

    render() {

        if (this.props.state.orderTotal === 0) {
            return (
                <div className="card text-center summary-cards">
                    <div className="card-header">
                        <ul className="nav nav-pills card-header-pills"></ul>
                    </div>
                    <div className="card-body">
                        <p className="lead pt-4">There are no items in your shopping cart.</p>
                    </div>
                </div>
            );
        }

        let content = (<ExtraBeverages />);

        if (this.props.state.beverageTotal > 0 || !this.props.state.extraBeverages.firstRender) {
            content = (
                <div>
                    <h2 style={{
                        margin: '0px',
                        paddingTop: '30px',
                        paddingBottom: '10px',
                        paddingLeft: '20px',
                        background: '#fff',
                        fontWeight: '400'
                    }}>Order Summary</h2>
                    <BreakfastCard />
                    <BeverageCard />
                    <DeliTrayCard />
                    <SaladSidesCard />
                    <SaladBoxCard />
                    <LunchBoxCard />
                    <DessertCard />
                    <SubSandwichCard />
                    
                    <div className="card text-center summary-cards">
                        <div className="card-body">

                            <div className="col-lg-4 col-sm-5 ml-auto p-0">
                                <table className="table table-clear">
                                    <tbody>
                                        <tr>
                                            <td className="left text-right" style={{fontWeight: '500'}}>
                                                Subtotal
                                            </td>
                                            <td className="right text-right">${this.props.state.orderTotal}</td>
                                        </tr>
                                        
                                        <tr>
                                            <td className="left text-right" style={{fontWeight: '500'}}>
                                                Tax (6.1%)
                                            </td>
                                            
                                            <td className="right text-right">
                                                ${this.state.tax}
                                            </td>
                                        </tr>
                                        
                                        <tr>
                                            <td className="left text-right" style={{fontWeight: '500'}}>
                                                Total
                                            </td>
                                            
                                            <td className="right text-right">
                                                <strong>${roundTo(this.props.state.orderTotal + this.state.tax, 2)}</strong>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>

                        </div>
                    </div>

                    <div className="card text-center summary-cards">
                        <div className="card-body">
                            <h3 className="card-title">Additional Notes <span>(Please provide any dietary restrictions/allergies, etc)</span></h3>
                            <textarea rows="5" className="form-control" onChange={this.handleTextarea} value={this.props.state.additionalNote} />
                        </div>
                    </div>

                    <div className="text-center mt-0" style={{ paddingTop: "10px", paddingBottom: "30px", backgroundColor: '#fff' }}>
                        <button className="btn btn-lg btn-orange" onClick={() => this.handleSubmit()}>Submit Order</button>
                    </div>
                </div>
            );
        }

        return (
            <div>
                {content}
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    state: state
});

export default connect(mapStateToProps)(Summary);



