import React, { Component } from 'react';
import { connect } from "react-redux";

import {
    LUNCHBOX_PRICE
} from '../../pricing.js';

import { Link } from "react-router-dom";

const roundTo = require('roundto');

class LunchBoxCard extends Component {
    
    rowElement = (name, quantity, chip, price, error, i) => {

        if (quantity === 0) {
            return null;
        }

        let errorClass = error ? 'table-danger' : '';
        let chip_names = ["Sun Chips Original", "Sun Chips Cheddar", "Sun Chips Garden Salsa", "Kettle BBQ", "Kettle Jalepeno Cheddar", "Kettle Sea Salt Vinegar", "Kettle Original", "Baked Original", "Baked BBQ"]


        return (
            <React.Fragment key={'lunchbox_' + i}>
                <tr className={errorClass}>
                    <td>{name}</td>
                    <td>{quantity}</td>
                    <td>{price}</td>
                    <td className="text-right">${roundTo(price * quantity, 2)}</td>
                </tr>
                <tr>
                    <td colSpan="4" className="pr-0" style={{textAlign: 'right'}}>
                        {
                            chip_names.map((name, index) => {
                                return (
                                    chip[name] !==0 ?
                                    <div class="btn btn-light pt-0 pb-0" key={'chip_' + index}>{name} <span class="badge badge-dark">{chip[name]}</span></div> : null
                                )
                            })
                        }
                    </td>
                </tr>
            </React.Fragment>
        );
    }


    calculateTotal = (items) => {
        let total = 0;

        items.forEach(item => {
            total += (LUNCHBOX_PRICE * item.qty);
        });

        return total;
    }


    whiteTable = (items) => {
        let total = this.calculateTotal(items);

        if (total === 0) return null;

        return (
            <table className="table">
                <thead>
                    <tr>
                        <th scope="col">Item</th>
                        <th scope="col">Quantity</th>
                        <th scope="col">Price</th>
                        <th scope="col" className="text-right">Total</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        items.map((item, i) => {
                            if (item.qty > 0) {
                                return this.rowElement(item.desc, item.qty, item.chips, LUNCHBOX_PRICE, false, i)                            }
                            return null;
                        })
                    }
                    <tr>
                        <td colSpan="3" className=""></td>
                        <th className="text-right">${roundTo(total, 2)}</th>
                    </tr>
                </tbody>
            </table>
        );
    }

    render() {

        if (this.props.state.lunchBoxTotal === 0)
            return null;

        let missing = (
            this.props.state.missingChip.batterUp + 
            this.props.state.missingChip.infieldFly +
            this.props.state.missingChip.safeCall +
            this.props.state.missingChip.fairBall +
            this.props.state.missingChip.outfielder +
            this.props.state.missingChip.groundRule
        );

        return (
            <div className="card text-center summary-cards">
                <div className="card-header">
                    <ul className="nav nav-pills card-header-pills">
                        <li className="nav-item">
                            <Link className="nav-link" to="/LunchBoxes">Edit</Link>
                        </li>
                    </ul>
                </div>
                <div className="card-body">
                    <h3 className="card-title">- Lunch Boxes</h3>

                    {
                        missing !== 0
                            ? <div className="alert alert-danger" role="alert">You must select 1 chip per lunch box ordered.</div>
                            : null
                    }

                    {
                        this.whiteTable([
                            {
                                desc: `Batter Up - roasted turkey breast`,
                                qty: this.props.state.batterUp,
                                chips: this.props.state.lunchBoxChip.batterUp
                            },
                            {
                                desc: `Infield Fly - chicken salad`,
                                qty: this.props.state.infieldFly,
                                chips: this.props.state.lunchBoxChip.infieldFly
                            },
                            {
                                desc: `Safe Call - albacore tuna salad	`,
                                qty: this.props.state.safeCall,
                                chips: this.props.state.lunchBoxChip.safeCall
                            },
                            {
                                desc: `Fair Ball - baked ham`,
                                qty: this.props.state.fairBall,
                                chips: this.props.state.lunchBoxChip.fairBall
                            },
                            {
                                desc: `Outfielder - roast beef`,
                                qty: this.props.state.outfielder,
                                chips: this.props.state.lunchBoxChip.outfielder
                            },
                            {
                                desc: `Ground Rule - veggie`,
                                qty: this.props.state.groundRule,
                                chips: this.props.state.lunchBoxChip.groundRule
                            }
                        ])
                    }

                    {/* <h3 className="card-title">Total: ${this.props.state.lunchBoxTotal}</h3> */}
                </div>
            </div>
        )
    }
}


const mapStateToProps = (state) => ({
    state: state
});

export default connect(mapStateToProps)(LunchBoxCard);


