import React, { Component } from 'react';
import { connect } from "react-redux";
import { SALADBOX_PRICE } from '../pricing';
import SaladBoxDressing from './SaladBoxDressing.js';

const roundTo = require('roundto');

class SaladBoxes extends Component {

    constructor(props) {
        super(props);

        this.props.dispatch({
            type: 'navIndex',
            payload: 4
        })

        this.handleInputChange = this.handleInputChange.bind(this);
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        let missingDressing = this.props.greenSalad.missingDressing;
        missingDressing[name] = (value - (this.props.greenSalad[name].dressing.Ranch + this.props.greenSalad[name].dressing.Italian + this.props.greenSalad[name].dressing.Caesar))


        this.props.dispatch({
            type: 'SaladBox',
            name: name,
            payload: Number(value),
            missingDressing: missingDressing
        });
    }


    render() {

        return (
            <React.Fragment>
            
                <div>

                    <div className="alert alert-warning">
                        <p className="lead">
                            Each boxed green salad includes a 6oz. salad,
                            choice of dressing (Ranch, Italian, Caesar), a roll,
                            pickle, cookie, piece of fruit and a unique toy.
                        </p>
                    </div>
                    {/* {
                        oneDressingPerSalad
                            ? null
                            : (<div className="alert alert-danger" role="alert">You must select 1 dressing per salad ordered.</div>)
                    } */}
                    <div className="white-table">
                        <form className="needs-validation" noValidate="">
                            <table className="table table-bordered">
                                <thead className="thead-orange">
                                    <tr>
                                        <th scope="col">Green Salad Boxes - ${SALADBOX_PRICE} ea.</th>
                                        <th scope="col">Quantity</th>
                                        <th scope="col">Total</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <th scope="row">Stolen Base
                                            <p className="text-muted">
                                                chicken, red bell pepper, bacon bits, blue cheese,
                                                boiled egg, grape tomato and romaine lettuce
                                            </p>
                                        </th>
                                        <td>
                                            <input
                                                name="cobSalad"
                                                type="number"
                                                min={0}
                                                className='form-control'
                                                aria-label="Quantity"
                                                value={this.props.greenSalad.cobSalad.qty}
                                                onChange={this.handleInputChange}
                                            />
                                            <SaladBoxDressing
                                                name="cobSalad"
                                            />
                                        </td>
                                        <td>${roundTo(this.props.greenSalad.cobSalad.qty * SALADBOX_PRICE, 2)}</td>
                                    </tr>

                                    <tr>
                                        <th scope="row">Base on Balls
                                            <p className="text-muted">
                                                boiled egg, grape tomato, shredded cheddar, monterey
                                                jack, ham, turkey, black olives and romaine lettuce
                                            </p>
                                        </th>
                                        <td>
                                            <input
                                                name="chefSalad"
                                                type="number"
                                                min={0}
                                                className='form-control'
                                                aria-label="Quantity"
                                                value={this.props.greenSalad.chefSalad.qty}
                                                onChange={this.handleInputChange}
                                            />
                                            <SaladBoxDressing
                                                name="chefSalad"
                                            />
                                        </td>
                                        <td>${roundTo(this.props.greenSalad.chefSalad.qty * SALADBOX_PRICE, 2)}</td>
                                    </tr>

                                    <tr>
                                        <th scope="row">Fowl Ball
                                            <p className="text-muted">
                                                chicken, soy nuts, parmesan cheese, pea sprouts
                                                and romaine lettuce
                                            </p>
                                        </th>
                                        <td>
                                            <input
                                                name="caesarSalad"
                                                type="number"
                                                min={0}
                                                className='form-control'
                                                aria-label="Quantity"
                                                value={this.props.greenSalad.caesarSalad.qty}
                                                onChange={this.handleInputChange}
                                            />
                                            <SaladBoxDressing
                                                name="caesarSalad"
                                            />
                                        </td>
                                        <td>${roundTo(this.props.greenSalad.caesarSalad.qty * SALADBOX_PRICE, 2)}</td>
                                    </tr>

                                    <tr>
                                        <th scope="row">Double Play
                                            <p className="text-muted">
                                                grape tomato, broccoli, radish, cucumber, carrot, cabbage
                                                and romaine lettuce
                                            </p>
                                        </th>
                                        <td>
                                            <input
                                                name="veggieSalad"
                                                type="number"
                                                min={0}
                                                className='form-control'
                                                aria-label="Quantity"
                                                value={this.props.greenSalad.veggieSalad.qty}
                                                onChange={this.handleInputChange}
                                            />
                                            <SaladBoxDressing
                                                name="veggieSalad"
                                            />
                                        </td>
                                        <td>${roundTo(this.props.greenSalad.veggieSalad.qty * SALADBOX_PRICE, 2)}</td>
                                    </tr>

                                    <tr>
                                        <td colSpan="2"></td>
                                        <td>${this.props.greenSalad.greenSaladTotal}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </form>
                    </div>
                </div>
            
            </React.Fragment>
        );
    }
}


const mapStateToProps = (state) => ({
    greenSalad: state
});

export default connect(mapStateToProps)(SaladBoxes);



