import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from "react-redux";
import { ROUTES } from '../routes';

class BottomNavbar extends Component {

  summaryPage = () => {
    return (
      <div className="bottom-panel d-flex justify-content-between">
        <div className="p-2"></div>
        <div className="p-2">
          <div className="display-4">
            <span>Total: ${this.props.state.orderTotal}</span>
          </div>
        </div>
        <div className="p-2"></div>
      </div>
    );
  }

  render() {

    let currentIndex = this.props.state.navIndex;

    if (currentIndex >= 8) {
      return null;
    }

    let prev = "#", next = "#";
    let prev_name = 'Prev', next_name = "Next";

    if (currentIndex !== 0) {
      prev = ROUTES[currentIndex - 1].link;
      // prev_name = ROUTES[currentIndex - 1].name;
    }

    if (currentIndex !== 8) {
      next = ROUTES[currentIndex + 1].link;
      // next_name = ROUTES[currentIndex + 1].name;
    }

    return (
      <div className="bottom-panel d-flex justify-content-between mt-0" >
        <div className="p-2">
          <div className="nav-buttons">
            <Link
              className={"btn btn-orange" + (this.props.state.navIndex === 0 ? ' disabled' : '')}
              to={prev}
            >{prev_name}</Link>
            <Link 
              className="btn btn-orange ml-3" 
              to={next}
            >{next_name}</Link>

            <Link 
              className="btn btn-orange ml-5" 
              to="Summary"
            >Review Order</Link>
          </div>
        </div>

        {/* <div className="p-2">
          <div className="display-4">
            <span>Subtotal: ${this.props.state.orderTotal}</span>
          </div>
        </div> */}

        <div className="p-2">
          <p style={{
            marginBottom: '0px',
            paddingRight: '21px',
            fontSize: '1.4rem',
            fontWeight: '600',
            lineHeight: '3.5rem'
          }}>Subtotal: ${this.props.state.orderTotal}</p>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  state: state
});

export default connect(mapStateToProps)(BottomNavbar);


