import React, { Component } from 'react';
import { connect } from "react-redux";
import { submitForm } from '../api';
import Loader from 'react-loader-spinner';

class Confirmation extends Component {

    constructor(props) {
        super(props);

        this.props.dispatch({
            type: 'navIndex',
            payload: 9
        })
    }

    componentDidMount() {
        this.props.submitForm(this.props.state);
    }

    render() {

        let load = (
            <div className='text-center' style={{padding: '20px'}}>
                <Loader
                    type="Bars"
                    color="#f59525"
                    height="100"
                    width="100"
                />
            </div>
        );

        let data;

        let confirmation = this.props.state.confirmation;

        if (confirmation.success) {
            data = (
                <React.Fragment>

                    <div className="card" style={{marginBottom: '0px'}}>
                        <div className="card-body">
                            <p className="card-title display-4">
                                Thank you
                            </p>
                            <div className="card-text">
                            <p className="lead">
                                Your order is confirmed. For your convenience, an email has been sent to {confirmation.email}
                            </p>
                                <table className="table">
                                    <thead>
                                        <tr>
                                            <td>Order ID</td>
                                            <td>{confirmation.orderID}</td>
                                        </tr>
                                    </thead>
                                </table>
                            </div>
                        </div>
                    </div>

                    <div className="card" style={{paddingTop: '15px', marginBottom: '15px'}}>
                        <div className="card-body">
                            
                            <div className="row">
                                <div className="col-md-4">
                                    <a href="/catering/online_order/agreement.php?r=ondeck" className="btn btn-orange btn-lg btn-block">Order Again</a>
                                </div>

                                <div className="col-md-4">
                                    <a href="/dining/sumc/ondeck" className="btn btn-orange btn-lg btn-block">Go to On Deck Deli</a>
                                </div>

                                <div className="col-md-4">
                                    <a href="/" className="btn btn-orange btn-lg btn-block">Go to Union</a>
                                </div>
                            </div>

                        </div>
                    </div>

                </React.Fragment>
            );
        }
        else
        {
            data = (
                <div className="card p-3" style={{marginBottom: '15px'}}>
                    <p style={{paddingTop: '15px', fontSize: '18px', textAlign: 'center'}}>
                        Something went wrong! If you haven't received an email, try to order again. Thank you.
                    </p>
                </div>
            )
        }

        let content = this.props.state.loading ? load : data;

        return <div>{content}</div>;
    }
}

const mapDispatchToProps = dispatch => (
    {
        submitForm: (state) => dispatch(submitForm(state)),
        dispatch: (obj) => dispatch(obj)
    }
)

const mapStateToProps = (state) => ({
    state: state
});

export default connect(mapStateToProps, mapDispatchToProps)(Confirmation);


