import React, { Component } from 'react';
import { connect } from "react-redux";

class LunchBoxChip extends Component {

    constructor(props) {
        super(props);
        this.state = {
            select: 'Select...'
        }

        this.handleSelectChip = this.handleSelectChip.bind(this);
    }

    handleSelectChip(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;

        this.props.dispatch({
            type: 'LunchBox_Chip',
            name: this.props.name,
            chip: value
        });

        this.setState({
            selectReset: 'Select...'
        })
    }

    handleRemoveChip(event) {
        if (event.target.dataset.chip !== undefined){
            this.props.dispatch({
                type: 'LunchBox_Chip_Remove',
                name: this.props.name,
                chip: event.target.dataset.chip
            });
        }
    }

    render() {
        return (
            <div className={'wrap-select-extra' + (this.props.globalState.missingChip[this.props.name] > 0 ? ' primary' : (this.props.globalState.missingChip[this.props.name] < 0 ? ' warning' : ''))}>

                {
                    this.props.globalState.missingChip[this.props.name] < 0 ?
                    <p><b>You need to remove chip</b></p> :
                    null
                }

                {
                    this.props.globalState.missingChip[this.props.name] > 0 ?
                    <div className="form-group row select-dressing">
                        <label className="col-sm-12 col-form-label" htmlFor={'select_' + this.props.name + '_dressing'}><b>Select {this.props.globalState.missingChip[this.props.name]} Chip</b></label>
                        <div className="col-sm-12">
                            <select
                                value={this.state.select}
                                className="form-control"
                                id={'select_' + this.props.name + '_chip'}
                                onChange={this.handleSelectChip}
                            >
                                <option>Select...</option>
                                {
                                    this.props.chips.map((chip,index) => {
                                        return <option key={'chip_option_'+index}>{chip}</option>
                                    })
                                }
                            </select>
                        </div>
                    </div>
                    :
                    null
                }

                {
                    this.props.chips.map((chip, index) => {
                        if (this.props.globalState.lunchBoxChip[this.props.name][chip] === 0)
                        {
                            return null;
                        }
                        else {
                            return (
                                <div className="badge badge-secondary saladbox-badge" key={'chip_badge_' + index}>

                                    <div className="badge-name">
                                        {chip}
                                        <div className="badge badge-light">{this.props.globalState.lunchBoxChip[this.props.name][chip]}</div>
                                    </div>

                                    <button type="button" className="close" data-chip={chip} onClick={this.handleRemoveChip.bind(this)}>
                                        <span aria-hidden="true" data-chip={chip}>x</span>
                                    </button>

                                </div>
                            )
                        }
                    })
                }

            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    globalState: state
});

export default connect(mapStateToProps)(LunchBoxChip);


