import React, { Component } from 'react';
import { connect } from "react-redux";

import {
    SALADBOX_PRICE
} from '../../pricing.js';

import { Link } from "react-router-dom";

const roundTo = require('roundto');

class SaladBoxCard extends Component {

    constructor(props) {
        super(props);
        this.dressings = this.dressings.bind(this);
    }

    rowElement = (name, detail, price, error, i) => {

        if (detail.qty === 0) {
            return null;
        }

        let errorClass = error ? 'table-danger' : '';

        return (
            <React.Fragment key={'saladbox' + i}>
                <tr className={errorClass}>
                    <td>{name}</td>
                    <td>{detail.qty}</td>
                    <td>{price}</td>
                    <td className="text-right">${roundTo(price * detail.qty, 2)}</td>
                </tr>
                <tr>
                    <td colSpan="4" className="pr-0" style={{textAlign: 'right'}}>
                        {
                            detail.dressing.Ranch !==0 ?
                            <div className="btn btn-light pt-0 pb-0">Ranch <span className="badge badge-dark">{detail.dressing.Ranch}</span></div> : null
                        }
                        {
                            detail.dressing.Italian !==0 ?
                            <div className="btn btn-light pt-0 pb-0">Italian <span className="badge badge-dark">{detail.dressing.Italian}</span></div> : null
                        }
                        {
                            detail.dressing.Caesar !==0 ?
                            <div className="btn btn-light pt-0 pb-0">Caesar <span className="badge badge-dark">{detail.dressing.Caesar}</span></div> : null
                        }
                    </td>
                </tr>
            </React.Fragment>
        );
    }


    calculateTotal = (items) => {
        let total = 0;

        items.forEach(item => {
            total += (SALADBOX_PRICE * item.detail.qty);
        });

        return total;
    }


    whiteTable = (items) => {
        let total = this.calculateTotal(items);

        if (total === 0) return null;

        return (
            <table className="table">
                <thead>
                    <tr>
                        <th className="thead-orange" colSpan={4}>Salads</th>
                    </tr>
                    <tr>
                        <th scope="col">Item</th>
                        <th scope="col">Quantity</th>
                        <th scope="col">Price</th>
                        <th scope="col" className="text-right">Total</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        items.map((item, i) => {
                            if (item.detail.qty > 0) {
                                return this.rowElement(item.desc, item.detail, SALADBOX_PRICE, false, i)
                            }
                            return null;
                        })
                    }
                    <tr>
                        <td colSpan="3" className=""></td>
                        <th className="text-right">${roundTo(total, 2)}</th>
                    </tr>
                </tbody>
            </table>
        );
    }

    dressingElement = (name, qty, error) => {
        if (qty === 0) return null;

        return (
            <tr className={error ? 'table-danger' : ''}>
                <td>{name}</td>
                <td>{qty}</td>
            </tr>
        );
    }

    dressings = (error) => {

        let total = (
            this.props.state.ranch
            + this.props.state.italian
            + this.props.state.caesar
        );

        if(total === 0) return null;

        return (
            <table className="table" style={{ marginTop: '15px' }}>
                <thead>
                    <tr>
                        <th scope="col" className="thead-orange" colSpan={3}>Dressings</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <th scope="col">Item</th>
                        <th scope="col">Quantity</th>
                    </tr>
                    {
                        this.dressingElement("Ranch", this.props.state.ranch, error)
                    }
                    {
                        this.dressingElement("Italian", this.props.state.italian, error)
                    }
                    {
                        this.dressingElement("Caesar", this.props.state.caesar, error)
                    }
                </tbody>
            </table>
        );
    }


    render() {

        let missing = (
            this.props.state.missingDressing.cobSalad + 
            this.props.state.missingDressing.chefSalad +
            this.props.state.missingDressing.caesarSalad +
            this.props.state.missingDressing.veggieSalad
        );

        if (this.props.state.greenSaladTotal === 0)
            return null;

        return (
            <div className="card text-center summary-cards">
                <div className="card-header">
                    <ul className="nav nav-pills card-header-pills">
                        <li className="nav-item">
                            <Link className="nav-link" to="/SaladBoxes">Edit</Link>
                        </li>
                    </ul>
                </div>
                <div className="card-body">
                    <h3 className="card-title">- Salad Boxes</h3>

                    {
                        missing !== 0
                            ? <div className="alert alert-danger" role="alert">You must select 1 dressing per salad ordered.</div>
                            : null
                    }

                    {
                        this.whiteTable([
                            {
                                desc: `Stolen Base - Cobb Salad`,
                                detail: this.props.state.cobSalad
                            },
                            {
                                desc: `Base on Balls - Chef Salad`,
                                detail: this.props.state.chefSalad
                            },
                            {
                                desc: `Fowl Ball - Caesar Salad`,
                                detail: this.props.state.caesarSalad
                            },
                            {
                                desc: `Double Play - Veggie Salad`,
                                detail: this.props.state.veggieSalad
                            }
                        ])
                    }

                    {/* {
                        this.dressings(missing !== 0)
                    } */}

                    {/* <h3 className="card-title">Total: ${this.props.state.greenSaladTotal}</h3> */}
                </div>
            </div>
        )
    }
}


const mapStateToProps = (state) => ({
    state: state
});

export default connect(mapStateToProps)(SaladBoxCard);


