import React, { Component } from 'react';
import { connect } from "react-redux";
import { Redirect, withRouter } from 'react-router-dom';

class ExtraBeverages extends Component {

    handleClick = (event) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;

        if (value === 'Yes'){
            this.props.history.push("/Beverages")
        }
        else {
            this.props.dispatch({
                type: 'EXTRA_BEVERAGES'
            });
        }
    }

    render() {

        return (
            <React.Fragment>
                {
                    (this.props.state.beverageTotal === 0) ?
                    <div className="card text-center summary-cards">
                        <div className="card-header">
                            <ul className="nav nav-pills card-header-pills"></ul>
                        </div>
                        <div className="card-body">
                            <h3 className="card-title mt-2">Extra Beverages</h3>
                            <div>
                                <p className="lead">Would you like to add extra beverages?</p>
                                <div className="container-fluid">
                                    
                                    <button className="btn btn-success" value="Yes" onClick={this.handleClick} style={{ margin: '10px' }}>Yes</button>
                                    <button className="btn btn-danger" value="No" onClick={this.handleClick} style={{ margin: '10px' }}>No</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    :
                    null
                }
            </React.Fragment>
        );
    }
}

const mapStateToProps = (state) => ({
    state: state
});

export default withRouter(connect(mapStateToProps)(ExtraBeverages));


